                                            <Dropdown disabled v-model="form.photo_sub_category_id" :options="props.photo_sub_categories.filter((x) => x.photo_category_id === form.photo_category_id)" optionValue="id" optionLabel="name" class="w-100"/>
                                        </div>
                                    </div>
                                    <Divider />
                                </div>

                                <div v-if="form.photo_category_id && form.photo_sub_category_id">
                                    <div class="row align-items-center mb-5">
                                        <label class="col-lg-5 fw-bold text-uppercase">Aile</label>
                                        <div class="col-md-7">
                                            <Dropdown disabled v-model="form.photo_family_id" :options="props.photo_families.filter((x) => x.photo_sub_category_id === form.photo_sub_category_id)" optionValue="id" optionLabel="name" class="w-100"/>
                                        </div>
                                    </div>
                                    <Divider />
                                </div>

                                <div v-if="form.photo_category_id && form.photo_sub_category_id && form.photo_family_id">
                                    <div class="row align-items-center mb-5">
                                        <label class="col-lg-5 fw-bold text-uppercase">Tür</label>
                                        <div class="col-md-7">
                                            <Dropdown disabled v-model="form.photo_type_id" :options="props.photo_types.filter((x) => x.photo_family_id === form.photo_family_id)" optionValue="id" optionLabel="name" class="w-100"/>
                                        </div>
                                    </div>
                                    <Divider />
                                </div>

                                <div class="row align-items-center mt-5">
                                    <label class="col-lg-5 fw-bold text-uppercase">Derinlik</label>
                                    <div class="col-md-7">
                                        <InputGroup>
                                            <InputGroupAddon>
                                                <span>m</span>
                                            </InputGroupAddon>
                                            <InputNumber class="w-100" v-model="form.depth" inputId="photoDepth" :useGrouping="false" disabled/>
                                        </InputGroup>
                                    </div>
                                </div>

                                <Divider />

                                <div class="row align-items-center mt-5">
                                    <label class="col-lg-5 fw-bold text-uppercase">Sıcaklık</label>
                                    <div class="col-md-7">
                                        <InputGroup>
                                            <InputGroupAddon>
                                                <span>°C</span>
                                            </InputGroupAddon>
                                            <InputNumber class="w-100" v-model="form.heat" inputId="photoHeat" :useGrouping="false" disabled/>
                                        </InputGroup>
                                    </div>
                                </div>

                                <div v-if="props.photo_attributes">
                                    <div v-for="(attribute, index) in form.attributes" :key="`photo-attribute-${index}`">
                                        <Divider />
                                        <div class="row align-items-center">
                                            <label class="col-lg-5 fw-bold text-uppercase">{{ attribute.name }}</label>
                                            <div class="col-md-7">

                                                <InputMask disabled mask="99:99" v-if="attribute.option_type == 'time'" v-model="attribute.value" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <InputMask disabled mask="99.99.9999" placeholder="GG.AA.YYYY" v-if="attribute.option_type == 'date'" v-model="attribute.value" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <InputMask disabled mask="99.99.9999 99:99" placeholder="GG.AA.YYYY HH:SS" v-if="attribute.option_type == 'datetime'" v-model="attribute.value" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <Dropdown disabled v-if="attribute.option_type == 'select'" v-model="attribute.value" :options="attribute.values" optionLabel="name" optionValue="name" placeholder="Lütfen seçiniz" class="w-100" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <MultiSelect disabled v-if="attribute.option_type == 'multiselect'" v-model="attribute.value" :options="attribute.values" optionLabel="name" optionValue="name" placeholder="Lütfen seçiniz" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <OptionType disabled :option="attribute" v-model="attribute.value" v-else :error-class="form.errors[`attributes.${index}.value`] ? true : false"/>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div class="card mb-5 mb-xl-10">
                            <div class="card-body">

                                <h5 class="card-title text-muted">Detaylar</h5>
                                <Divider class="mb-10"/>

                                <Textarea disabled autoResize rows="5" cols="30" class="w-100" v-model="form.summary"/>

                            </div>
                        </div>
                        <div class="card mb-5 mb-xl-10">
                            <div class="card-body">

                                <h5 class="card-title text-muted">Konum Bilgileri</h5>
                                <Divider class="mb-10"/>

                                <div v-show="mapVisible" class="h-300px w-100 mt-5" ref="mapContainer"></div>

                            </div>
                        </div>
                    </div>
                    <div class="col-xl-3">
                        <div class="card mb-5 mb-xl-10">
                            <div class="card-body">

                                <h5 class="card-title text-muted">Ana Görsel</h5>
                                <Divider class="mb-5"/>
