                                        <div class="overlay-wrapper rounded">
                                            <img :src="form.mainImage.original_url" class="img-fluid rounded" />
                                        </div>
                                        <div class="overlay-layer bg-dark bg-opacity-25">
                                            <a class="btn btn-sm btn-light-primary btn-icon" data-fancybox="gallery" :href="form.mainImage.original_url">
                                                <i class="bi bi-eye fs-3"></i>
