                    </div>
                </div>

            </form>
        </div>
    </div>
    
</template>

<script setup>

import {ref,shallowRef,onMounted, nextTick} from "vue";
import { useForm, usePage } from "@inertiajs/vue3";
import Divider from "primevue/divider";
import InputText from "primevue/inputtext";
import MultiSelect from "primevue/multiselect";
import Dropdown from "primevue/dropdown";
import Calendar from "primevue/calendar";
import OptionType from "../../Components/OptionType";
import InputMask from "primevue/inputmask";
import InputNumber from "primevue/inputnumber";
import InputGroup from 'primevue/inputgroup';
import InputGroupAddon from 'primevue/inputgroupaddon';
import Textarea from "primevue/textarea";
import InputSwitch from "primevue/inputswitch";

import { Fancybox } from "@fancyapps/ui";
import "@fancyapps/ui/dist/fancybox/fancybox.css";

const props = defineProps({
    photo_categories:Object,
    photo_sub_categories:Object,
    photo_families:Object,
    photo_types:Object,
    photo:Object,
    photo_attributes:Object
});

const editor = ref(null);

const approval_statuses = ref([
    {
        label:'Yeni',
        value:0
    },
    {
        label:'Onaylandı',
        value:1
    },
    {
        label:'İptal',
        value:2
    }
]);

let google_api_key = usePage().props.google_api_key;

const autocompleteInput = ref(null);
const mapContainer = ref(null);
const map = shallowRef(null);
const marker = ref(null);
const mapVisible = ref(false);
const placeDetails = ref(null);

const photoAttributeIds = props.photo_attributes.map((x) => {
    return x.id;
});

const form = useForm({
    language:props.photo.language,
    name:props.photo.name,
    summary:props.photo.summary,
    shooting_date: new Date(props.photo.shooting_date),
    photo_category_id:props.photo.photo_type?.photo_family?.photo_sub_category?.photo_category?.id,
    photo_sub_category_id:props.photo.photo_type?.photo_family?.photo_sub_category?.id,
    photo_family_id:props.photo.photo_type?.photo_family?.id,
    photo_type_id:props.photo.photo_type_id,
    attributes:props.photo.attributes.filter((x) => photoAttributeIds.includes(x.id)),
    depth:props.photo.depth,
    heat:props.photo.heat,
    coordinates:props.photo.coordinates,
    latitude:props.photo.latitude,
    longitude:props.photo.longitude,
    country:props.photo.country,
    city:props.photo.city,
    county:props.photo.county,
    address:props.photo.address,
    mainImage:props.photo.main_image,
    galleryImages:props.photo.gallery_images ?? [],
    approval_status:props.photo.approval_status,
    is_published:props.photo.is_published,
    usable_for_science:props.photo.usable_for_science,
    not_approve_reason:props.photo.not_approve_reason
});


const updateMap = async (lat, lng) => {
    // DOM'un hazır olduğundan emin olun
    await nextTick();

    if (!map.value) {
        // Harita oluşturma
        map.value = new google.maps.Map(mapContainer.value, {
            center: { lat, lng },
            zoom: 13,
            panControl: false,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            rotateControl: false,
            mapId:'24a6df6e5eacbfee'
        });

        // Marker oluşturma

        const { AdvancedMarkerElement } = await google.maps.importLibrary("marker");
        marker.value = new AdvancedMarkerElement({
            position: { lat, lng },
            map: map.value,
            gmpDraggable: false,
        });

    } else {
        // Harita ve marker güncelleme
        map.value.setCenter({ lat, lng });
        marker.value.position = { lat, lng }; // Yeni pozisyonu güncelle
    }

    mapVisible.value = true;
};


const initMap = () => {
    if( props.photo.latitude && props.photo.longitude ){

        setTimeout(() => {
            updateMap(props.photo.latitude,props.photo.longitude);
            mapVisible.value = true;
        }, 1000);

    }
}

// Google Maps API'yi dinamik olarak yükleme fonksiyonu
const loadGoogleMapsAPI = () => {
    return new Promise((resolve, reject) => {
    if (window.google) {
        resolve(window.google);
        initMap();
    } else {
        const script = document.createElement('script');
        script.src = `https://maps.googleapis.com/maps/api/js?key=${google_api_key}&libraries=places,marker`;
        script.async = true;
        script.onload = () => resolve(window.google);
        script.onerror = (error) => reject(error);
        document.head.appendChild(script);
        initMap();
    }
    });
};


onMounted(() => {

    loadGoogleMapsAPI().catch((error) => {
        console.error("Google Maps API yüklenirken hata oluştu:", error);
    });

    Fancybox.bind('[data-fancybox="gallery"]', {
    //
    });

});

</script>